<template>
  <div class="file-manager">
    <!--  -->
    <div class="mode-switch">
      <button
          :class="{ active: mode === 'registration' }"
          @click="switchMode('registration')"
      ></button>
      <button
          :class="{ active: mode === 'bid' }"
          @click="switchMode('bid')"
      ></button>
    </div>

    <h2>{{ mode === 'registration' ? '' : '' }}</h2>
    <!--  -->
    <div class="filter-container">
      <label for="category-select"></label>
      <select id="category-select" v-model="selectedCategory">
        <option value=""></option>
        <option v-for="cat in categories" :key="cat" :value="cat">
          {{ cat }}
        </option>
      </select>
    </div>

    <!--  -->
    <table class="file-table">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(file, index) in filteredFiles" :key="file.filePath">
        <td>{{ index + 1 }}</td>
        <td class="file-name">{{ file.materialName }}</td>
        <td>{{ formatDate(file.uploadTime) }}</td>
        <td>{{ formatDate(file.expiryDate) }}</td>
        <td>
          <button @click="previewPdf(file)" class="btn-preview"></button>
          <button @click="editFile(file)" class="btn-edit"></button>
          <button @click="deleteFile(file)" class="btn-delete"></button>
        </td>
      </tr>
      <tr v-if="filteredFiles.length === 0">
        <td colspan="5" class="no-data"></td>
      </tr>
      </tbody>
    </table>

    <!-- PDF  -->
    <div
        v-if="showPreview"
        class="preview-overlay"
        @click.self="closePreview"
    >
      <div class="preview-container">
        <div class="preview-header">
          <span>{{ currentFile.materialName }}</span>
          <button @click="closePreview" class="close-btn"></button>
        </div>
        <div class="preview-body">
          <iframe
              v-if="pdfBlobUrl"
              :src="pdfBlobUrl"
              frameborder="0"
              class="pdf-viewer"
          ></iframe>
          <p v-else>...</p>
        </div>
      </div>
    </div>

    <!--  -->
    <div
        v-if="showConfirm"
        class="confirm-overlay"
        @click.self="showConfirm = false"
    >
      <div class="confirm-box">
        <p> {{ fileToDelete.materialName }} </p>
        <div class="confirm-actions">
          <button @click="confirmDelete" class="btn-confirm-yes"></button>
          <button @click="showConfirm = false" class="btn-confirm-no">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "FileManager",
  data() {
    return {
      mode: 'registration',    // registration  bid
      files: [],               // 
      categories: [],          //  files 
      selectedCategory: "",   // 
      showPreview: false,      //  PDF /
      currentFile: {},         // 
      pdfBlobUrl: null,        //  <iframe>  Blob URL
      showConfirm: false,      // /
      fileToDelete: {},        // 
    };
  },
  computed: {
    filteredFiles() {
      if (!this.selectedCategory) {
        return this.files;
      }
      return this.files.filter((file) => {
        const parts = file.materialName.split("/");
        return parts[0] === this.selectedCategory;
      });
    },
  },
  created() {
    this.fetchFiles();
  },
  methods: {
    // 
    switchMode(mode) {
      if (this.mode === mode) return;
      this.mode = mode;
      this.selectedCategory = '';
      this.fetchFiles();
    },

    //  mode 
    async fetchFiles() {
      try {
        const url = this.mode === 'registration'
            ? '/api/materials/getRegistrationAll'
            : '/api/materials/getAllBid';
        const resp = await axios.get(url);
        const arr = Array.isArray(resp.data.materials)
            ? resp.data.materials
            : [];
        this.files = arr;
        this.extractCategories();
      } catch (err) {
        console.error("", err);
        this.files = [];
        this.categories = [];
      }
    },

    //  files  /  categories
    extractCategories() {
      const set = new Set();
      this.files.forEach((file) => {
        const parts = file.materialName.split("/");
        if (parts.length > 1) {
          set.add(parts[0]);
        } else {
          set.add(file.materialName);
        }
      });
      this.categories = Array.from(set);
    },

    //  ISO  YYYY-MM-DD hh:mm
    formatDate(isoString) {
      if (!isoString) return "-";
      const d = new Date(isoString);
      const YYYY = d.getFullYear();
      const MM = String(d.getMonth() + 1).padStart(2, "0");
      const DD = String(d.getDate()).padStart(2, "0");
      const hh = String(d.getHours()).padStart(2, "0");
      const mm = String(d.getMinutes()).padStart(2, "0");
      return `${YYYY}-${MM}-${DD} ${hh}:${mm}`;
    },

    // / URL
    downloadUrl(filePath) {
      return `/api/materials/download?filePath=${encodeURIComponent(
          filePath
      )}`;
    },

    //  PDF
    async previewPdf(file) {
      if (!file.filePath || !file.filePath.toLowerCase().endsWith(".pdf")) {
        alert(" PDF");
        return;
      }
      this.currentFile = file;
      this.showPreview = true;
      this.pdfBlobUrl = null;

      try {
        const resp = await axios.get(this.downloadUrl(file.filePath), {
          responseType: "blob",
        });
        const blob = new Blob([resp.data], { type: "application/pdf" });
        this.pdfBlobUrl = URL.createObjectURL(blob);
      } catch (err) {
        console.error(" PDF ", err);
        alert("");
        this.closePreview();
      }
    },

    // 
    closePreview() {
      this.showPreview = false;
      if (this.pdfBlobUrl) {
        URL.revokeObjectURL(this.pdfBlobUrl);
        this.pdfBlobUrl = null;
      }
      this.currentFile = {};
    },

    // 
    deleteFile(file) {
      this.fileToDelete = file;
      this.showConfirm = true;
    },

    // 
    async confirmDelete() {
      try {
        const url = this.mode === 'registration'
            ? '/api/materials/deleteRegistrationFile'
            : '/api/materials/deleteBid';
        await axios.delete(url, {
          params: { materialName: this.fileToDelete.materialName },
        });
        this.showConfirm = false;
        this.fileToDelete = {};
        await this.fetchFiles();
      } catch (err) {
        console.error("", err);
        alert("");
      }
    },

    // 
    async editFile(file) {
      const newName = prompt(
          " /.pdf",
          file.materialName
      );
      if (!newName || newName.trim() === "" || newName === file.materialName) {
        return;
      }

      const originalExpiry = file.expiryDate;
      try {
        const url = this.mode === 'registration'
            ? '/api/materials/reRegistrationName'
            : '/api/materials/reBidName';
        await axios.put(
            url,
            null,
            {
              params: {
                oldName: file.materialName,
                newName: newName.trim(),
                expiryDate: originalExpiry,
              },
            }
        );
        await this.fetchFiles();
        alert("");
      } catch (err) {
        console.error("", err);
        alert("");
      }
    },
  },
  beforeUnmount() {
    if (this.pdfBlobUrl) {
      URL.revokeObjectURL(this.pdfBlobUrl);
    }
  },
};
</script>

