<template>
  <div class="employee-manager">
    <h2></h2>

    <!--  -->
    <div class="filter-container" v-if="positions.length">
      <label for="position-select"></label>
      <select id="position-select" v-model="selectedPosition">
        <option value=""></option>
        <option v-for="pos in positions" :key="pos" :value="pos">
          {{ pos }}
        </option>
      </select>
    </div>

    <!--  -->
    <table class="employee-table">
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(emp, index) in filteredEmployees" :key="emp.id">
        <td>{{ index + 1 }}</td>
        <td class="emp-name">{{ emp.name }}</td>
        <td>{{ emp.position }}</td>
        <td>
          <button @click="viewEmployee(emp)" class="btn-view"></button>
          <button @click="editEmployee(emp)" class="btn-edit"></button>
          <button @click="deleteEmployee(emp)" class="btn-delete"></button>
        </td>
      </tr>
      <tr v-if="filteredEmployees.length === 0">
        <td colspan="4" class="no-data"></td>
      </tr>
      </tbody>
    </table>

    <!--  idnameposition -->
    <div
        v-if="showDetail"
        class="detail-overlay"
        @click.self="closeDetail"
    >
      <div class="detail-container">
        <div class="detail-header">
          <span>{{ currentEmployee.name }}</span>
          <button @click="closeDetail" class="close-btn"></button>
        </div>
        <div class="detail-body">
          <p><strong>ID</strong> {{ currentEmployee.id }}</p>
          <p><strong></strong> {{ currentEmployee.name }}</p>
          <p><strong></strong> {{ currentEmployee.position }}</p>
        </div>
      </div>
    </div>

    <!--  -->
    <div
        v-if="showConfirm"
        class="confirm-overlay"
        @click.self="showConfirm = false"
    >
      <div class="confirm-box">
        <p> {{ employeeToDelete.name }} </p>
        <div class="confirm-actions">
          <button @click="confirmDelete" :disabled="deleting" class="btn-confirm-yes">
            {{ deleting ? "" : "" }}
          </button>
          <button @click="showConfirm = false" :disabled="deleting" class="btn-confirm-no">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "EmployeeManager",
  data() {
    return {
      employees: [],            // { id, name, position } 
      positions: [],            // 
      selectedPosition: "",     // 
      showDetail: false,
      currentEmployee: {},
      showConfirm: false,
      employeeToDelete: {},
      deleting: false
    };
  },
  computed: {
    //  selectedPosition 
    filteredEmployees() {
      if (!this.selectedPosition) {
        return this.employees;
      }
      return this.employees.filter(
          (emp) => emp.position === this.selectedPosition
      );
    }
  },
  created() {
    this.fetchEmployees();
  },
  methods: {
    //  GET /employees/all 
    async fetchEmployees() {
      try {
        //  /api  "/api/employees/all"
        const resp = await axios.get("/api/employees/all");
        const arr = Array.isArray(resp.data) ? resp.data : [];
        //  id, name, position 
        this.employees = arr.map((item) => ({
          id: item.employeeID,
          name: item.employeesName,
          position: item.employeesPosition
        }));
        this.extractPositions();
      } catch (err) {
        console.error("", err);
        this.employees = [];
        this.positions = [];
      }
    },

    //  position 
    extractPositions() {
      const set = new Set();
      this.employees.forEach((emp) => {
        if (emp.position) {
          set.add(emp.position);
        }
      });
      this.positions = Array.from(set);
    },

    // 
    viewEmployee(emp) {
      this.currentEmployee = emp;
      this.showDetail = true;
    },

    // 
    closeDetail() {
      this.showDetail = false;
      this.currentEmployee = {};
    },

    // 
    deleteEmployee(emp) {
      this.employeeToDelete = emp;
      this.showConfirm = true;
    },

    //  DELETE /employees/delete/{id} 
    async confirmDelete() {
      if (!this.employeeToDelete.id) return;
      this.deleting = true;
      try {
        //  /api `/api/employees/delete/${id}`
        await axios.delete(`/api/employees/delete/${this.employeeToDelete.id}`);
        this.showConfirm = false;
        this.employeeToDelete = {};
        await this.fetchEmployees();
      } catch (err) {
        console.error("", err);
        alert(err.response?.data?.message || "");
      } finally {
        this.deleting = false;
      }
    },

    //  prompt 
    async editEmployee(emp) {
      const newPosition = prompt(
          ` ${emp.name} `,
          emp.position || ""
      );
      if (!newPosition || newPosition.trim() === "" || newPosition === emp.position) {
        return;
      }
      try {
        //  PUT /employees/update?id={id}&position={newPosition}
        await axios.put("/api/employees/update", null, {
          params: {
            id: emp.id,
            position: newPosition.trim()
          }
        });
        await this.fetchEmployees();
        alert("");
      } catch (err) {
        console.error("", err);
        alert("");
      }
    }
  }
};
</script>

