<template>
  <main class="enhanced-container">
    <!-- / -->
    <section class="function-card">
      <div class="card-header">
        <span class="icon"></span>
        <h2></h2>
      </div>
      <div class="card-body">
        <div class="action-group">
          <!--  -->
          <div v-for="f in fields" :key="f.id" class="field-row">
            <div class="field-row-head">
              <label class="field-name">{{ f.label }}</label>
              <button class="mini-btn danger" type="button" @click="removeField(f.id)"></button>
            </div>
            <input
                class="modern-input"
                :type="f.type"
                v-model="f.value"
                :placeholder="`${f.label}`"
            />
          </div>

          <!--  -->
          <div class="add-field">
            <h3 class="add-title"></h3>
            <div class="add-grid">
              <input class="modern-input" v-model="newField.label" placeholder="" />
              <select class="modern-input" v-model="newField.type">
                <option value="text"></option>
                <option value="number"></option>
                <option value="date"></option>
                <option value="email"></option>
              </select>
              <input class="modern-input" :type="newField.type" v-model="newField.value" placeholder="" />
              <button class="gradient-btn success" type="button" @click="addField"></button>
            </div>
            <p v-if="fieldError" class="error-message">{{ fieldError }}</p>
          </div>
        </div>
      </div>
    </section>

    <!--  -->
    <section class="function-card">
      <div class="card-header">
        <span class="icon"></span>
        <h2></h2>
      </div>
      <div class="card-body">
        <div class="upload-section">
          <label class="file-dropzone" @dragover.prevent @drop.prevent="onDrop">
            <div class="upload-content">
              <div class="upload-icon"></div>
              <div><strong> DOCX </strong> .docx</div>
              <div v-if="selectedFile">{{ selectedFile.name }}{{ (selectedFile.size/1024).toFixed(1) }} KB</div>
              <!--  -->
            </div>
            <input
                ref="fileInput"
                type="file"
                accept=".docx,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                @change="onFileChange"
            />
          </label>

          <!--  -->
          <div class="preview-section">
            <div class="preview-header">
              <div></div>
              <div class="action-buttons">
                <button class="gradient-btn secondary" type="button" @click="copyInfoString" :disabled="!infoString"></button>
              </div>
            </div>
            <div class="preview-content">
              <pre v-if="infoString">{{ infoString }}</pre>
              <div v-else class="loading-message"></div>
            </div>
          </div>

          <div class="action-buttons">
            <button class="gradient-btn secondary" type="button" @click="resetAll"></button>
            <button
                class="gradient-btn primary"
                type="button"
                @click="handleSubmit"
                :class="{ 'disabled-button': !selectedFile }"
                :disabled="!selectedFile"
            >
              
            </button>
          </div>

          <p v-if="error" class="error-message">{{ error }}</p>
          <p v-if="successMsg" class="success-message">{{ successMsg }}</p>
        </div>
      </div>
    </section>
  </main>
</template>

<script setup>
import { reactive, ref, computed } from 'vue'

/** =========  ========= */
const uid = () => (Date.now().toString(36) + Math.random().toString(36).slice(2, 8))
const deepCopy = (arr) => arr.map(o => ({ ...o }))

// 
const seedFields = [
  { id: uid(), label: '', type: 'text',  value: '' },
  { id: uid(), label: '', type: 'text',  value: '' },
  { id: uid(), label: '',     type: 'date',  value: '' },
  { id: uid(), label: '/////', type: 'text',  value: '' },
  { id: uid(), label: '', type: 'text',  value: '8' },
  { id: uid(), label: '', type: 'text',  value: '' },
  { id: uid(), label: '/', type: 'text', value: '37050168617109555666' },
  { id: uid(), label: '', type: 'text',  value: '272000' },
  { id: uid(), label: '', type: 'text',  value: '' },
  { id: uid(), label: '', type: 'date',  value: '20050401' },
  { id: uid(), label: '', type: 'text',  value: '' },
  { id: uid(), label: '',     type: 'text',  value: '0537-3150005' },
  { id: uid(), label: '', type: 'text',  value: '' },
  { id: uid(), label: '',     type: 'text',  value: '' },
  { id: uid(), label: '',     type: 'number',value: '56' },
  { id: uid(), label: '',     type: 'text',  value: '' },
  { id: uid(), label: '', type: 'text', value: '230103197605166839' },
  { id: uid(), label: '', type: 'text',  value: '0537-3150537' },
  { id: uid(), label: '', type: 'email', value: 'zlxtkjgf@163.com' }
]

const fields = ref(deepCopy(seedFields))
const fieldError = ref('')
const newField = reactive({ label: '', type: 'text', value: '' })

// ==========  ==========
const selectedFile = ref(null)
const fileInput = ref(null)
const error = ref('')
const successMsg = ref('')

// ==========  ==========
const infoString = computed(() => {
  return fields.value
      .map(f => {
        let val = String(f.value ?? '').trim()
        if (!val) return ''
        //  date 
        if (f.type === 'date') {
          // 
          const d = new Date(val)
          if (!isNaN(d)) {
            const yyyy = d.getFullYear()
            const mm = String(d.getMonth() + 1).padStart(2, '0')
            const dd = String(d.getDate()).padStart(2, '0')
            val = `${yyyy}${mm}${dd}`
          }
        }
        return `${f.label}${val}`
      })
      .filter(Boolean)
      .join('\n')
})


// ==========  ==========
function addField() {
  fieldError.value = ''
  const label = (newField.label || '').trim()
  if (!label) { fieldError.value = ''; return }
  if (fields.value.some(f => f.label === label)) { fieldError.value = ''; return }
  fields.value.push({ id: uid(), label, type: newField.type, value: newField.value })
  newField.label = ''
  newField.type = 'text'
  newField.value = ''
}
function removeField(id) {
  fields.value = fields.value.filter(f => f.id !== id)
}

// ==========  ==========
function onDrop(e) {
  const f = e.dataTransfer?.files?.[0]
  if (!f) return
  onFileChange({ target: { files: [f] } })
}
function onFileChange(e) {
  error.value = ''
  successMsg.value = ''
  const file = e.target.files?.[0]
  if (!file) { selectedFile.value = null; return }
  if (file.size > 5 * 1024 * 1024) { error.value = ' 5MB'; clearFile(); return }
  const isDocx = file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document' || /\.docx$/i.test(file.name)
  if (!isDocx) { error.value = ' .docx '; clearFile(); return }
  selectedFile.value = file
}
function clearFile() {
  selectedFile.value = null
  if (fileInput.value) fileInput.value.value = ''
}

// ==========  ==========
function handleSubmit() {
  error.value = ''
  successMsg.value = ''
  if (!selectedFile.value) { error.value = ' .docx '; return }

  const fd = new FormData()
  fd.append('file', selectedFile.value)
  fd.append('info', infoString.value || '')

  // fire-and-forget
  submitToUploadBs(fd)

  // 
  successMsg.value = ''
  clearFile()

  // 
  // router.push({ name: 'Preview' })
}
function submitToUploadBs(formData) {
  fetch('/api/BidGeneration/uploadBs', { method: 'POST', body: formData })
      .then(resp => {
        if (!resp.ok) console.error('', resp.status, resp.statusText)
      })
      .catch(err => console.error('', err))
}

async function copyInfoString() {
  if (!infoString.value) return
  try {
    if (navigator.clipboard?.writeText) {
      await navigator.clipboard.writeText(infoString.value)
    } else {
      const ta = document.createElement('textarea')
      ta.value = infoString.value
      ta.style.position = 'fixed'
      ta.style.left = '-9999px'
      document.body.appendChild(ta)
      ta.select()
      document.execCommand('copy')
      document.body.removeChild(ta)
    }
    successMsg.value = ''
  } catch (err) {
    error.value = ''
  } finally {
    setTimeout(() => (successMsg.value = ''), 2000)
  }
}

function resetAll() {
  fields.value = deepCopy(seedFields)
  error.value = ''
  successMsg.value = ''
  clearFile()
}
</script>

